<template>
  <!---->
  <div class="about">
    <!-- catturo l'evento search lanciato in header -->
    <Search @searchQuery="searching" :setDefault="setDefault" />
    <!-- aggiungo un contenitore con background e descrizione pagina che visualizzo solo all'accesso -->
    <!-- utilizzo una condizione -->
    <div v-if="welcome" class="welcome_c">
      <div class="welcome">
        <div class="mc_bool">
          <img src="../assets/img/boolflix.png" alt="boolflix" />
          <h3>Unlimited movies, TV shows, and more.</h3>
          <h4>What am I watching tonight?</h4>
          <button @click.prevent="popular()" class="btn btn-primary mc_btn">
            Top 20
          </button>
        </div>
      </div>
    </div>

    <div
      class="d-flex justify-content-center align-items-center not_found text-center flex-column"
      v-if="this.errors == true"
    >
      <h2>Sever offline.</h2>
      <br>
      <p>We apologize for the inconvenience, but we are experiencing technical difficulties. We are working hard to try and resolve the issue as soon as possible.<br>Thank you for your understanding.</p>
    </div>

    <div v-if="popularList">
      <Popular
        :filmList="filmList"
        :filmData="filmData"
        :popMovies="popMovies"
        :tvList="tvList"
        :tvData="tvData"
        :popTv="popTv"
        :apiPage="apiPage"
        @pagPrec="pagPrec"
        @pagSucc="pagSucc"
      />
    </div>

    <div class="d-flex justify-content-center align-items-center mc_msg">
      <circles-to-rhombuses-spinner
        v-if="loading"
        :animation-duration="1200"
        :circles-num="3"
        :circle-size="15"
        color="#ff1d5e"
      />
    </div>

    <!-- se show è true mostro l'h1 e il relativo messaggio -->
    <div class="d-flex justify-content-center align-items-center mc_msg">
      <h1 v-if="show" class="text-center">No results found</h1>
      <!-- <h1 v-if='results.movie.length === 0 && results.movie.length === 0'>nessun risultato</h1> -->
    </div>

    <div v-if="!loading">
      <Main
        class="main"
        :filmList="filmList"
        :filmData="filmData"
        :popMovies="popMovies"
        :tvList="tvList"
        :tvData="tvData"
        :popTv="popTv"
        :apiPage="apiPage"
        @pagPrec="pagPrec"
        @pagSucc="pagSucc"
      />
    </div>
  </div>
</template>
<script>
// importo in app...
import axios from "axios";
import Search from "../components/Boolflix/Search.vue";
import Main from "../components/Boolflix/Main.vue";
import Popular from "../components/Boolflix/Popular.vue";
import { CirclesToRhombusesSpinner } from "epic-spinners";
export default {
  name: "BoolflixView",
  // dichiaro i componenti esportati in APP
  components: {
    Search,
    Main,
    Popular,
    CirclesToRhombusesSpinner,
  },
  data() {
    return {
      apiMoviesURL: "https://api.themoviedb.org/3/search/movie",
      apiTvURL: "https://api.themoviedb.org/3/search/tv",
      apiTrendURL: "https://api.themoviedb.org/3/trending/",
      apiKey: process.env.VUE_APP_BOOLFLIX_API_KEY,
      apiLang: "en-EN",
      welcome: true, // se true mostro un elemento di default con background di 'benvenuto'. Dopo la chiamata API diventa false
      show: false, // utilizzo la variabile show per mostrare o no il messaggio 'nessun rislutato trovato'
      apiPage: 1,
      apiQuery: "",
      filmList: [],
      tvList: [],
      filmData: {},
      tvData: {},
      popMovies: [],
      popTv: [],
      loading: false,
      popularList: false,
      errors: false,
    };
  },
  mounted () {
    window.scrollTo(0, 0)
  },
  methods: {
    // funzione che permette di eseguire la chiamata API avviando la ricerca di query(del testo inserito dall'utente)
    searching(apiQuery) {
      if (apiQuery != "") {
        this.show = false;
        this.popularList = false;
        this.errors = false;
        this.loading = true;
        this.apiQuery = apiQuery;
        let request = {
          params: {
            api_key: this.apiKey,
            query: this.apiQuery,
            language: this.apiLang,
            apiPage: this.apiPage,
          },
        };
        axios
          .all([
            axios.get(this.apiMoviesURL, request),
            axios.get(this.apiTvURL, request),
          ])
          .then(
            axios.spread((resMovies, resTv) => {
              this.loading = false;
              this.welcome = false;
              this.filmData = resMovies.data;
              this.filmList = resMovies.data.results;
              console.log(this.filmList);
              this.tvData = resTv.data;
              this.tvList = resTv.data.results;
              console.log(this.tvList);
              if (this.filmList.length === 0 && this.tvList.length === 0) {
                this.show = true; // v-if='show = true' mostra il messaggio 'Nessun risultato trovato'
              }
            })
          )
          .catch((err) => {
            this.loading = false;
            this.errors = true;
            console.log(err);
          });
      }
    },
    // metodi scatenati al click dei bottoni prec. e succ. per ripetere la chiamata
    // API con numero di pagina aggiornato
    pagPrec(apiPage) {
      this.apiPage = apiPage;
      this.searching(this.apiQuery);
    },
    pagSucc(apiPage) {
      this.apiPage = apiPage;
      this.searching(this.apiQuery);
    },
    popular() {
      this.loading = true;
      this.welcome = false;
      this.popularList = true;
      axios
        .all([
          axios.get(
            "https://api.themoviedb.org/3/trending/movie/week?api_key=" +
              this.apiKey
          ),
          axios.get(
            "https://api.themoviedb.org/3/trending/tv/week?api_key=" +
              this.apiKey
          ),
        ])
        .then(
          axios.spread((resPopMovies, resPopTv) => {
            this.loading = false;
            this.popMovies = resPopMovies.data.results;
            console.log(this.popMovies);
            this.popTv = resPopTv.data.results;
            console.log(this.popTv);
          })
        )
        .catch((err) => {
          console.log(err);
        });
    },
    //funzione per resettare gli array
    reset() {
      this.filmList = [];
      this.tvList = [];
      this.popMovies = [];
      this.popTv = [];
      this.welcome = true;
    },
    //funzione per la situazione di default,viene passata come prop per il bottone reset
    setDefault() {
      this.reset();
      //this.popular();
    },
  },
};
</script>

<style lang="scss">
// importo il foglio di stile general.scss
@import "../assets/style/app.scss";
// definisco il colore header
$color-header: #303030;
// definisco il colore per i bottoni
$color-btn: #e30d12;
.about {
  overflow: hidden;
  min-height: 100vh;
  padding-top: 0px;
  background: $color-header;
  @media only screen and (max-width: 992px) {
    padding-top: 0px;
  }
    @media only screen and (max-width: 450px) {
    padding-top: 50px;
  }
}
.main {
  padding-bottom: 150px;
  margin: 0 auto;
  @media all and (max-width: 600px) {
    padding-bottom: 50px;
  }
  @media all and (min-width: 601px) and (max-width: 750px) {
    padding-bottom: 62px;
  }
  @media all and (min-width: 751px) and (max-width: 800px) {
    padding-bottom: 141px;
  }
  @media all and (min-width: 801px) and (max-width: 900px) {
    padding-bottom: 150px;
  }
}
.welcome_c {
  display: flex;
  align-items: center;
  //height: 50vh;
  margin-top: 100px;
  @media all and (max-width: 800px) {
    //height: 60vh;
  }
}
.welcome {
  width: 100%;
  //height: 100%;
  //background-image: url("../assets/img/boolflix.jpg");
  background-color: #303030;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
}
.mc_bool {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 50px;
  h1 {
    font-size: 3em;
    color: $color-btn;
    text-transform: uppercase;
    text-shadow: 0px 3px 2px rgba(12, 10, 10, 0.767);
  }
  h3 {
    font-size: 3em;
    @media all and (max-width: 400px) {
      font-size: 1em;
    }
    @media all and (max-width: 500px) {
      font-size: 2.2em;
    }
  }
  h4 {
    margin-bottom: 40px;
  }
  img {
    @media all and (max-width: 400px) {
      width: 70%;
    }
  }
}
.mc_msg {
  height: 75vh;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.main-t {
  padding-bottom: 140px;
  @media all and (max-width: 799px) {
    padding-bottom: 60px;
  }
}
</style>
