<template>
  <ul
    class="m-3 p-2 p-2"
    @click="modalShow = !modalShow"
    v-b-modal.modal-scrollable
  >
    <li class="d-flex justify-content-between">
      <div>
        <span>Title: </span>
        <!-- a secoda che si cerchi un film o una serie tv inserisco entrambi i percorsi da visualizzare.
          film e serie tv hanno campi di risposta differenti (title e name) 
          Utilizzo un 'or logico ||'-->
        <h2>{{ card.title || card.name }}</h2>
      </div>
      <div class="d-flex flex-column align-items-center">
        <img src="../../assets/img/boolflix.png" alt="boolflix" />
      </div>
    </li>

    <li>
      <span>Original Title: </span>
      <!-- a secoda che si cerchi un film o una serie tv inserisco entrambi i percorsi da visualizzare.
        film e serie tv hanno campi di risposta differenti (original_title e original_name) 
        Utilizzo un 'or logico ||'-->
      <h3>{{ card.original_title || card.original_name }}</h3>
    </li>

    <li>
      <span class="d-block">Original Language: </span>
      <h4 class="d-inline">{{ card.original_language }}</h4>
      <!-- se original_language è uguale a 'en' o 'it' aggiungo l'immagine di una bandierina -->
      <!-- binding di src che concatena il percorso dell'immagine + original_language + estensione immagine-->
      <!-- img-fluid rende responsive l'immagine -->
      <img
        v-if="card.original_language == 'en' || card.original_language == 'it' || card.original_language == 'fr'"
        :src="require(`../../assets/img/flag-${card.original_language}.png`)"
        :alt="'flag-' + card.original_language"
        class="img-fluid"
      />
      <!-- percorso alternativo :src img -->
      <!-- :src="require('../assets/img/flag-'+[card.original_language]+'.png')"  -->
    </li>

    <li>
      <span>Vote Average: </span>
      <!-- se vote_average non è una stringa vuota... -->
      <!-- richiamo la funzione che arrotonda vote.average e lo trasforma in un numero da 1 a 5 -->
      <!-- con position mostro 5 stelle vuote e sopra tante stelle piene in base al voto-->
      <!-- <h4>{{getCeil(card.vote_average)}} </h4> -->
      <!-- {{card.vote_average/2}} -->
      <div v-if="card.vote_average != ''" class="star">
        <div class="star_empty">
          <i class="far fa-star"></i>
          <i class="far fa-star"></i>
          <i class="far fa-star"></i>
          <i class="far fa-star"></i>
          <i class="far fa-star"></i>
        </div>
        <div class="star_full">
          <!-- stampo direttamente tante stelline quanto è il numero del voto con un v-for -->
          <i
            v-for="(star, index) in getCeil(card.vote_average)"
            :key="index"
            class="fas fa-star"
          >
          </i>
        </div>
      </div>
    </li>

    <li>
      <span>Overview: </span>
      <!-- richiamo la funzione che taglia la stringa dopo un n numeri di caratteri aggiungendo ...-->
      <p>
        {{ substringText(card.overview, 0, 100) + "..." }}
      </p>
    </li>

    <!-- aggiungo l'immaginde di copertina... -->
    <!-- <img :src="('https://image.tmdb.org/t/p/w342'+[card.poster_path])" alt=""> -->
    <!-- solo se l'oggetto ha un'immagine di coperina -->
    <!--
    <img
      v-if="card.poster_path != null"
      class="img-fluid mc_poster"
      :src="getUrl()"
      :alt="`poster ${card.original_title}`"
    />-->
    <img class="img-fluid mc_poster" :src="imgFunction" alt="" />

    <b-modal
      v-model="modalShow"
      id="modal-tall"
      title="Boolflix"
      centered
      scrollable
      ok-only
      hide-header-close
      :header-bg-variant="headerBgVariant"
      :header-text-variant="headerTextVariant"
      :body-bg-variant="bodyBgVariant"
      :body-text-variant="bodyTextVariant"
      :footer-bg-variant="footerBgVariant"
      :footer-text-variant="footerTextVariant"
    >
      <h3 class="text-center">{{ card.title || card.name }}</h3>
      <p class="text-center">{{ card.release_date || card.first_air_date }}</p>
      <div v-if="card.vote_average != ''" class="text-center">
        <div class="text-center" style="color: darkgoldenrod">
          <!-- stampo direttamente tante stelline quanto è il numero del voto con un v-for -->
          <i
            v-for="(star, index) in getCeil(card.vote_average)"
            :key="index"
            class="fas fa-star"
          >
          </i>
        </div>
      </div>
      <b-img
        class="img-fluid mc_modal text-center height-fit"
        center
        :src="imgFunction"
        alt=""
      />
      <br>
      <p>{{ card.overview }}</p>
    </b-modal>
  </ul>
</template>

<script>
export default {
  name: "CardComp",
  data: () => ({
    showElement: false,
    modalShow: false,
    variants: [
      "primary",
      "secondary",
      "success",
      "warning",
      "danger",
      "info",
      "light",
      "dark",
    ],
    headerBgVariant: "dark",
    headerTextVariant: "danger",
    bodyBgVariant: "light",
    bodyTextVariant: "dark",
    footerBgVariant: "warning",
    footerTextVariant: "dark",
  }),
  props: {
    card: Object,
  },
  computed: {
    imgFunction() {
      if (this.card.poster_path === null) {
        //return require("../../assets/img/fotoNotFound.jpg");
        return;
      } else {
        return this.getUrl();
      }
    },
  },
  methods: {
    // funzione per trasformare il voto in numero intero da 1 a 5
    getCeil(num) {
      let n = Math.ceil(num) / 2;
      return Math.ceil(n);
    },
    // funzione che restituisce il percorso dell'immagine poster
    getUrl() {
      let url = "https://image.tmdb.org/t/p/w342";
      let path = [this.card.poster_path];
      return url + path;
    },
    // funzione che fa visulaizzare solo i primi 100 caratteri di un testo aggiungendo alla fine '...'
    substringText(string, min, max) {
      return string.substring(min, max);
    },
  },
};
</script>

<style lang="scss" scoped>
// importo le variabili
// definisco il colore header
$color-header: #303030;
// definisco il colore per i bottoni
$color-btn: #e30d12;
ul {
  box-shadow: 0px 0px 3px white;
  position: relative;
  background-color: $color-header;
  list-style: none;
  cursor: pointer;
  transition: all 1.8s;
  margin-bottom: 50px;
  padding: 30px;
  height: 350px;
  overflow-y: hidden;
  overflow-wrap: break-word;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: 230px;
  &:hover {
    filter: brightness(1.4);
    transform: scale(1.1);
  }
  li {
    text-overflow: ellipsis;
    overflow: hidden;
    span {
      color: $color-btn;
      font-size: 0.8em;
      text-transform: uppercase;
    }
    h2,
    h3,
    h4 {
      font-size: 0.9em;
      text-overflow: ellipsis;
    }
    p {
      font-size: 0.9em;
    }
    img {
      height: 1.5rem;
      padding-bottom: 7px;
      margin-left: 7px;
    }
    div.star {
      position: relative;
      .star_empty {
        position: absolute;
        color: darkgoldenrod;
      }
      .star_full {
        color: darkgoldenrod;
      }
    }
  }
  img.mc_poster {
    display: block;
    position: absolute;
    top: 0%;
    left: 0;
    height: 100%;
    width: 100%;
    transition: all 0.6s;
    &:hover {
      filter: opacity(0);
    }
    &.no_img {
      opacity: 0.5;
    }
  }
  // Modal
  .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    color: var(--bs-modal-color);
    pointer-events: auto;
    background-color: #303030;
    background-clip: padding-box;
    border: var(--bs-modal-border-width) solid var(--bs-modal-border-color);
    border-radius: var(--bs-modal-border-radius);
    outline: 0;
  }
  .star {
    position: relative;
    .star_empty {
      position: absolute;
      color: darkgoldenrod;
    }
    .star_full {
      color: darkgoldenrod;
    }
  }
}
</style>
